#index-header {
    display: none;
  }

    #mobile-header {
    position: sticky;
    top: 0;
    z-index: 10;
    width: 100%;
    border-bottom: solid 4px var(--main-color);
    display: block;

}

#mobile-nav {
  background: linear-gradient(
    to right,
    var(--main-color),
    var(--third-color)
  );
  width: 100%;
}

#mobile-nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  height: 70px;
  box-sizing: border-box;
}

#mobile-logo {
  height: 100%;
  display: flex;
  align-items: center;

}

#mobile-logo img {
  height: 100%;
  border: solid white 2px;
  transition: 0.1s;
  width: 130px;
}

#mobile-logo img:hover {
  border-color: var(--secondary-color);
}

#mobile-menu-btn {
  font-size: 2rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

#mobile-menu {
  background-color: var(--third-color);
  height: 100vh;
  width: 99%;
  z-index: 3;
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  border: solid var(--fourth-color) 2px;
  margin-left: 0.5%;
  overflow: hidden;
display: none;
}

#mobile-menu-close{
  width: 100%;
  font-size: 30px;
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 3%;
  box-sizing: border-box;
  cursor: pointer;
  height: 7vh;
  margin-bottom: 0;

}

#mobile-menu-links{

  margin-top: 0;
  list-style: none;
  width: 100%;
  height: 63%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-left: 0;
  margin-bottom: 0;
}

.mobile-menu-links-li {
  border-bottom: solid var(--fourth-color) 1.5px;
  width: 100%;
  cursor: pointer;
  height: 12.5%;
}

.mobile-menu-links-li-a{
  text-decoration: none;
  color: var(--fourth-color);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

#mobile-menu-links-selected{
  background-color: var(--fourth-color);
}
#mobile-menu-links-selected a {
  color: var(--third-color);
}

#mobile-menu-info{
  margin-top: 0;
  background-color: black;
  width: 100%;
  height: 30%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.mobile-menu-info{
  width: 100%;
  height: 22%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  box-sizing: border-box;
}

.mobile-menu-info-div1{
  height: 100%;
  min-width: 30%;

  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 2%;
  box-sizing: border-box;
}

.mobile-menu-info-div1 h4 {
  color: white;
  margin-left: 4%;
}

.mobile-menu-info-anchor{

  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 1%;
  overflow-x: scroll;
  text-decoration: none;
  color: white;

}

#mobile-menu-info-social{
  border: solid white 2px;
  width: 100%;
  height: 30%;
  box-sizing: border-box;
  margin-top: 0;
  display: flex ;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-left: 0;
  list-style: none;
}

.mobile-menu-info-social-li{
  height: 100%;
  color: var(--secondary-color);
  margin-left: 1%;
}


.mobile-menu-info-social-li2{
  height: 100%;
  margin-left: 3%;
}

.mobile-menu-info-social-li2-a{
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mobile-menu-info-social-li2-img{
  height:  20px;
  width: 20px;
}

/* CONTACT MOBILE MAIN  */

#contact-main{
  display: none;
}

#contact-main-mobile {
  border: solid 5px var(--third-color);
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: scroll;
  box-sizing: border-box;
}

/* title */
#contact-main-mobile-h1 {
  background-color: var(--fourth-color);
  color: var(--third-color);
  min-height: 12%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* info section */
#contact-main-mobile-info {
  width: 95%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contact-main-mobile-article {
  border-bottom: solid 2px var(--third-color);
  padding-bottom: 0.5rem;
}

.contact-main-mobile-h2 {
  background-color: var(--third-color);
  color: var(--fourth-color);
  padding: 0.5rem;
  font-size: 130%;
}

.contact-main-mobile-p {
  background-color: var(--fourth-color);
  padding: 0.5rem;
  font-size: 115%;
}

.contact-main-mobile-link {
  color: var(--third-color);
  text-decoration: underline;
}

/* form */
#contact-main-mobile-form {
  width: 95%;
  margin-top: 1rem;
}

#contact-main-mobile-fieldset {
  border: solid 2px var(--main-color);
  border-radius: 12px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

#contact-main-mobile-legend {
  background-color: var(--main-color);
  color: var(--fourth-color);
  padding: 0.5rem 1rem;
  font-size: 120%;
}

.contact-main-mobile-label {
  background-color: var(--third-color);
  color: var(--fourth-color);
  padding: 0.4rem;
  font-size: 110%;
}

.contact-main-mobile-input,
.contact-main-mobile-textarea {
  width: 100%;
  border: solid 1px var(--third-color);
  padding: 0.5rem;
  font-size: 110%;
  box-sizing: border-box;
}

.contact-main-mobile-textarea {
  min-height: 120px;
  min-width: 100%;

}

/* buttons */
.contact-main-mobile-buttons {
  display: flex;
  justify-content: space-between;
  gap: 0.5rem;
}

.contact-main-mobile-btn {
  width: 48%;
  padding: 0.6rem;
  font-size: 110%;
  cursor: pointer;
}

.contact-main-mobile-btn.primary {
  background-color: var(--main-color);
  color: var(--fourth-color);
  border: none;
}

.contact-main-mobile-btn.secondary {
  background-color: white;
  color: var(--third-color);
  border: solid 2px var(--third-color);
}

/* message */
#contact-main-mobile-message {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  margin-left: 25%;
}

.main-mobile-message-success{
  background-color: green;
  color: white;
  font-size: 16px;
  padding: 1.5%;
}
.main-mobile-message-error{
  background-color: red;
  color: white;
  font-size: 16px;
  padding: 1.5%;
}

/*   FOOTER */
#index-footer{
  display: none;
}
#index-footer-mobile {
  width: 100%;
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  margin-bottom: 0px;
    padding: 2%;
}

#index-footer-mobile-div1 {
  text-align: center;

}

#index-footer-mobile-p1 {
  font-size: 13px;
  line-height: 1.4;
  margin: 0;
  
}


#index-footer-mobile-div2 {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;

}

#index-footer-mobile-p2 {
  font-size: 13px;
  margin: 0;
}

#index-footer-mobile-a1 {
  font-size: 13px;
  color: white;
  text-decoration: underline;
  text-decoration-color: white;
  padding: 6px 10px;
  border-radius: 6px;
  transition: 0.3s;
}

#index-footer-mobile-a1:hover {
  background-color: var(--third-color);
  color: var(--fourth-color);
}

/* ADMIN */
#index-footer-mobile-div3 {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

}

#index-footer-mobile-p2{
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-decoration: none;
  color: var(--fourth-color);
}
#index-footer-mobile-p2:hover{
  color: white;
}
