#index-header {
  display: none;
}

#mobile-header {
  z-index: 10;
  border-bottom: solid 4px var(--main-color);
  width: 100%;
  display: block;
  position: sticky;
  top: 0;
}

#mobile-nav {
  background: linear-gradient(to right, var(--main-color), var(--third-color));
  width: 100%;
}

#mobile-nav-bar {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  padding: .5rem 1rem;
  display: flex;
}

#mobile-logo {
  align-items: center;
  height: 100%;
  display: flex;
}

#mobile-logo img {
  border: 2px solid #fff;
  width: 130px;
  height: 100%;
