  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

#mobile-menu-close {
  color: #fff;
  box-sizing: border-box;
  cursor: pointer;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  height: 7vh;
  margin-bottom: 0;
  padding-right: 3%;
