}

#mobile-menu-info {
  box-sizing: border-box;
  background-color: #000;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  height: 30%;
  margin-top: 0;
  display: flex;
}

.mobile-menu-info {
  box-sizing: border-box;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 22%;
